import React, {PropTypes} from 'react';
import KlassName from 'teapan/klassName';

const noop = () => {};

const FADE_KINDS = ['in'];

class Fade extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			mounted: !props.transitionAppear,
		};

		this.onEnter = this.onEnter.bind(this);
		this.onLeave = this.onLeave.bind(this);
	}

	onEnter(callback) {
		return () => {
			callback();
			if (this.props.onEnter) {
				this.props.onEnter();
			}
		};
	}

	onLeave(callback) {
		return () => {
			callback();
			if (this.props.onLeave) {
				this.props.onLeave();
			}
		};
	}

	componentWillAppear(callback) {
		if (!this.props.transitionAppear) {
			this.onEnter(callback)();
		}

		setTimeout(this.onEnter(callback), this.props.transitionAppearTimeout);
	}

	componentDidAppear() {
		this.setState({
			mounted: true
		});
	}

	componentWillEnter(callback) {
		if (!this.props.transitionEnter) {
			this.onEnter(callback)();
		}

		setTimeout(this.onEnter(callback), this.props.transitionEnterTimeout);
	}

	componentDidEnter() {
		this.setState({
			mounted: true
		});
	}

	componentWillLeave(callback) {
		this.setState({
			mounted: false
		});

		if (!this.props.transitionLeave) {
			this.onLeave(callback)();
		}

		setTimeout(this.onLeave(callback), this.props.transitionLeaveTimeout);
	}

	render() {
		let {
			kind,
			className,
			...others
		} = this.props;

		const klassName = KlassName('fade',{
			['fade--' + kind]:this.state.mounted,
		},className);

	return (
			<div {...others} className={klassName} />
		);
	}
};

Fade.propTypes = {
	className: PropTypes.string,

	transitionAppearTimeout: PropTypes.number,
	transitionEnterTimeout: PropTypes.number,
	transitionLeaveTimeout: PropTypes.number,
	transitionAppear: PropTypes.bool,
	transitionEnter: PropTypes.bool,
	transitionLeave: PropTypes.bool,

	kind: PropTypes.oneOf(FADE_KINDS),

	onEnter: PropTypes.func,
	onLeave: PropTypes.func
};

Fade.defaultProps = {
	kind: 'in',
	transitionAppearTimeout: 0,
	transitionEnterTimeout: 0,
	transitionLeaveTimeout: 0,
	transitionAppear: true,
	transitionEnter: true,
	transitionLeave: true,
	onEnter: noop,
	onLeave: noop
};

export default Fade;